<template>
     <div class="modal fade in" id="editVendorUnitInfo" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" style="display: none;">
    <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content">
            <div class="modal-header">
                <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true"></span></button>
                <h4 class="modal-title" id="exampleModalLabel"></h4>
            </div>
            <div class="modal-body forms">
                <div class="form-inline">
                    <div class="row">
                        <div class="col-sm-2">
                            <label class="control-label" for="formNo"></label>
                            <span class="require-label">*</span>
                        </div>
                        <div class="col-sm-10">
                            <input v-validate="'required'" name="name" type="text" id="name" class="form-control" placeholder="" v-model="$parent.$data.vendorUnitModel.name.name" v-bind:class="{'haserror': errors.has('name')}">
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-sm-2">
                            <label class="control-label" for="formNo"></label>
                        </div>
                        <div class="col-sm-10">
                            <input type="text" id="description" class="form-control" placeholder="" v-model="$parent.$data.vendorUnitModel.desc">
                        </div>
                    </div>                    
                    <div class="row">
                        <div class="col-sm-2">
                            <label class="control-label"></label>
                        </div>
                        <div class="col-sm-4">
                            <input type="text" id="shortCode" class="form-control" placeholder="" maxlength="50" v-model="$parent.$data.vendorUnitModel.abbr">
                        </div>
                        <div class="col-sm-2">
                            <label class="control-label"></label>
                            <span class="require-label">*</span>
                        </div>
                        <div class="col-sm-4">
                            <input v-validate="'required'" name="contactPerson" type="text" id="contactPerson" class="form-control" placeholder="" maxlength="50" v-model="$parent.$data.vendorUnitModel.contactName.name" v-bind:class="{'haserror': errors.has('contactPerson') }">
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-sm-2">
                            <label class="control-label"></label>
                        </div>
                        <div class="col-sm-10">
                            <input type="text" id="address" class="form-control" placeholder="" maxlength="50" v-model="$parent.$data.vendorUnitModel.contactAddress">
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-sm-2">
                            <label class="control-label">1</label>
                        </div>
                        <div class="col-sm-4">
                            <input type="text" id="contactWay1" class="form-control" placeholder="1" maxlength="50" v-model="$parent.$data.vendorUnitModel.contactMethod1">
                        </div>
                        <div class="col-sm-2">
                            <label class="control-label">2</label>
                        </div>
                        <div class="col-sm-4">
                            <input type="text" id="contactWay2" class="form-control" placeholder="2" maxlength="50" v-model="$parent.$data.vendorUnitModel.contactMethod2">
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-sm-2">
                            <label class="control-label">3</label>
                        </div>
                        <div class="col-sm-4">
                            <input type="text" id="contactWay3" class="form-control" placeholder="3" maxlength="50" v-model="$parent.$data.vendorUnitModel.contactMethod3">
                        </div>
                        <div class="col-sm-2">
                            <label class="control-label">4</label>
                        </div>
                        <div class="col-sm-4">
                            <input type="text" id="contactWay4" class="form-control" placeholder="4" maxlength="50" v-model="$parent.$data.vendorUnitModel.contactMethod4">
                        </div>
                    </div>
                </div>
            </div>
            <div class="modal-footer">
                <label id="error" class="label-danger"><span v-show="errors.has('name')||errors.has('contactPerson')" class="help is-danger"></span></label>
                <button type="button" class="btn btn-primary" id="btnConfirm" @click="submitEdit"></button>
                <button type="button" class="btn btn-default" data-dismiss="modal" id="btnClose"></button>
            </div>
        </div>
    </div>
</div>
</template>

<script>
export default {
  name: 'editVendorUnit',
  data () {
    return {    
         }
  },
  methods:{   
       //
      submitEdit(){
        var url = this.GLOBAL.hostIp;
        var _this = this.$parent.$data;
              //     
              this.$validator.validateAll().then(() => {
        // eslint-disable-next-line
                    var data = {
                      Id:_this.vendorUnitModel.id,
                      Name:_this.vendorUnitModel.name.name,
                      Description:_this.vendorUnitModel.desc,
                      ShortCode:_this.vendorUnitModel.abbr,
                      ContactInfo:{
                          ContactPerson:_this.vendorUnitModel.contactName.name,
                          Address:_this.vendorUnitModel.contactAddress,
                          ContactWay1:_this.vendorUnitModel.contactMethod1,
                          ContactWay2:_this.vendorUnitModel.contactMethod2,
                          ContactWay3:_this.vendorUnitModel.contactMethod3,
                          ContactWay4:_this.vendorUnitModel.contactMethod4,
                      },          
                      ParentId:_this.parentId       
                  };
                     this.$http.post(url+'/VendorSetting/SaveVendorUnit',data,{emulateJSON: true,credentials: true}).then(response => {
                      
                    },response => {
                            // error callback
                            console.log("")
                          });
                  $("#editVendorUnitInfo").modal("toggle")
      }).catch(() => {
        // eslint-disable-next-line
      }); 
       
        
       
      },
  }
}
</script>
<style>  
</style>


