<template>
     <div class="modal fade" id="productInfo" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" style="display: none;">
    <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content">
            <div class="modal-header">
                <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true"></span></button>
                <h4 class="modal-title" id="exampleModalLabel"></h4>
            </div>
            <div class="modal-body forms">
                <div class="form-inline">
                    <div class="row">
                        <div class="col-sm-2">
                            <label class="control-label"></label>
                            <span class="require-label">*</span>
                        </div>
                        <div class="col-sm-4">
                            <input v-validate="'required'" name="name" v-bind:class="{'haserror': errors.has('name')}" type="text" id="name" class="form-control" required="" placeholder="" maxlength="50" v-model="$parent.$data.productModel.name.value" >
                        </div>
                        <div class="col-sm-2">
                            <label class="control-label"></label>
                        </div>
                        <div class="col-sm-4">
                            <input type="text" id="fullName" class="form-control" placeholder="" maxlength="100" v-model="$parent.$data.productModel.wholeName">
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-sm-2">
                            <label class="control-label"></label>
                            <span class="require-label">*</span>
                        </div>
                        <div class="col-sm-4">
                            <input v-validate="'required'" name="registerNumber" v-bind:class="{'haserror': errors.has('registerNumber')}" type="text" id="registerNumber" class="form-control" required="" placeholder="" maxlength="50" v-model="$parent.$data.productModel.registerNumber">
                        </div>
                        <div class="col-sm-2">
                            <label class="control-label"></label>
                            <span class="require-label">*</span>
                        </div>
                        <div class="col-sm-4">
                            <input v-validate="'required'" name="validDate" v-bind:class="{'haserror': errors.has('validDate')}" type="text" id="validDate" class="form-control" placeholder="" maxlength="100" v-model="$parent.$data.productModel.validDate">
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-sm-2">
                            <label class="control-label"></label>
                        </div>
                        <div class="col-sm-4">
                            <input type="text" id="shortCode" class="form-control" placeholder="" maxlength="50" v-model="$parent.$data.productModel.abbr">
                        </div>
                        <div class="col-sm-2">
                            <label class="control-label"></label>
                        </div>
                        <div class="col-sm-4">
                            <input type="text" id="brand" class="form-control" placeholder="" maxlength="50" v-model="$parent.$data.productModel.brand">
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-sm-2">
                            <label class="control-label"></label>
                        </div>
                        <div class="col-sm-4">
                            <input type="text" id="miniPackageSpec" class="form-control" placeholder="" maxlength="50" v-model="$parent.$data.productModel.miniPackageSpec">
                        </div>
                        <div class="col-sm-2">
                            <label class="control-label"></label>
                            <span class="require-label">*</span>
                        </div>
                        <div class="col-sm-4">
                            <input v-validate="'required'" name="miniPackageUnit" v-bind:class="{'haserror': errors.has('miniPackageUnit')}" type="text" id="miniPackageUnit" class="form-control" required="" placeholder="" maxlength="10" v-model="$parent.$data.productModel.miniPackageUnit.value" >
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-sm-2">
                            <label class="control-label"></label>
                            <span class="require-label">*</span>
                        </div>
                        <div class="col-sm-4">
                            <input v-validate="'required'" name="miniPackageCount" v-bind:class="{'haserror': errors.has('miniPackageCount')}" type="text" id="miniPackageCount" class="form-control" required="" placeholder="" maxlength="10" v-model="$parent.$data.productModel.miniPackageCount.value">
                        </div>
                        <div class="col-sm-2">
                            <label class="control-label"></label>
                        </div>
                        <div class="col-sm-4">
                            <input type="text" id="packageUnit" class="form-control" placeholder="" maxlength="10" v-model="$parent.$data.productModel.standardUnit">
                        </div>
                    </div>                    
                    <div class="row">
                        <div class="col-sm-2">
                            <label class="control-label"></label>
                        </div>
                        <div class="col-sm-4">
                            <input type="text" id="category" class="form-control" placeholder="" maxlength="30" v-model="$parent.$data.productModel.class">
                        </div>
                        <div class="col-sm-2">
                            <label class="control-label"></label>
                        </div>
                        <div class="col-sm-4">
                            <input type="checkbox" id="isLocal" checked="checked" v-model="$parent.$data.productModel.domestic">
                        </div>
                    </div>
                </div>
            </div>
            <div class="modal-footer">
                <label id="error" class="label-danger"><span v-show="errors.has('name')||errors.has('registerNumber')||errors.has('validDate')||errors.has('miniPackageUnit')||errors.has('miniPackageCount')" class="help is-danger"></span></label>
                <button type="button" id="btnConfirm" class="btn btn-primary" @click="applyAddProduct"></button>
                <button type="button" id="btnClose" class="btn btn-default" data-dismiss="modal"></button>
            </div>
        </div>
    </div>
</div>
</template>

<script>
export default {
  name: 'addProducts',
  data () {
    return {             
    }
  },
  methods:{
     applyAddProduct(){     
        var url = this.GLOBAL.hostIp;         
              //      
       
    this.$validator.validateAll().then(() => {
        // eslint-disable-next-line
        var data = {
            id:0,
            Name:this.$parent.$data.productModel.name.value,
            FullName:this.$parent.$data.productModel.wholeName,
            registerNumber:this.$parent.$data.productModel.registerNumber,
            validDate:this.$parent.$data.productModel.validDate,
            ShortCode:this.$parent.$data.productModel.abbr,
            Brand:this.$parent.$data.productModel.brand,
            MiniPackageSpec:this.$parent.$data.productModel.miniPackageSpec,
            MiniPackageUnit:this.$parent.$data.productModel.miniPackageUnit.value,
            MiniPackageCount:this.$parent.$data.productModel.miniPackageCount.value,
            PackageUnit:this.$parent.$data.productModel.standardUnit,
            Category:this.$parent.$data.productModel.class,
            IsLocal:this.$parent.$data.productModel.domestic
      };
       this.$http.post(url+"/Product/Save",data,{emulateJSON: true,credentials: true}).then(response => {               
                // get body data                               
              }, response => {
                // error callback
              });
      $("#productInfo").modal("toggle")
      }).catch(() => {
        // eslint-disable-next-line        
      });
         
      
    },
    }
}
</script>
<style>  
</style>


